
html,
body
{
  height: 100%;
}
#app
{
  min-height: 100%;
}
#app .btn,
#app .btn-group
{
  margin: 5px;
}
#app .btn-group .btn
{
  margin: 0;
}
#app .btn i
{
  font-size: 0.8em;
}
#main-content
{
  padding-bottom: 0;
}
@media screen and ( max-width: 768px ){
#app
  {
    background: #eceeef;
}
#app .options .btn
  {
    padding: 3px 10px;
    font-size: 0.875rem;
}
#app .results h4
  {
    font-size: 1.2rem;
}
#app .options
  {
    overflow-y: scroll;
    padding: 5px;
    height: 130px;
}
#app .options .btn-group
  {
    background: #fff;
    border-bottom: 1px solid #eceeef;
    padding: 0px 0 10px 0;
    position: relative;
    display: block;
    text-align: left;
}
#app .options .btn-group:first-child
  {
    border-top: 1px solid #eceeef;
    padding-top: 10px;
}
#app .options .btn-group .btn:first-child
  {
    width: 30px;
    height: 30px;
}
#app .btn-group .btn-secondary
  {
    border: none;
    background: none;
    box-shadow: none;
}
#app .btn-group .btn:first-child
  {
    border-radius: 50%;
}
#app .result-chain-list
  {
    display: block;
}
#app .result-chain .btn
  {
    padding: 0 5px;
}
#app .result-chain .btn-danger
  {
    width: 15px;
    height: 15px;
    line-height: 15px;
    padding: 0;
}
#app .results
  {
    padding: 20px;
    margin-left: -15px;
    margin-right: -15px;
    background: #eceeef;
}
#app #calculator-container
  {
    background: #ffffff;
}
}



